body {
  min-height: 100vh;
  min-width: min-content;
  --margin-size: .25em;
  --border-width: 2px;
  --setting-spacing: 1.25em;
  flex-direction: column;
  margin: 0;
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 500;
  display: flex;
}

canvas {
  background-color: #0000;
  position: absolute;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  font-family: inherit;
  font-size: inherit;
  cursor: pointer;
  border: 0;
  padding: 0;
}

button:focus {
  outline: none;
}

input[type="number"] {
  font-family: inherit;
  font-size: inherit;
  width: 5em;
  text-align: right;
  -moz-appearance: textfield;
  border: 1px solid #000;
  border-radius: .25em;
  margin-left: .5em;
  margin-right: .15em;
}

input[type="number"]::-webkit-inner-spin-button, input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

input[type="number"]:focus {
  outline: none;
}

input[type="number"]:invalid {
  border-color: red;
}

input[type="number"]:disabled {
  opacity: .5;
}

#app {
  flex-directio: row;
  flex-wrap: wrap;
  flex-grow: 1;
  display: flex;
}

#canvases {
  left: var(--margin-size);
  top: var(--margin-size);
  position: absolute;
}

#canvases > canvas {
  margin: var(--border-width);
}

#simulation {
  margin: var(--margin-size);
  flex-grow: 0;
  flex-shrink: 0;
}

#viewport {
  border: solid var(--border-width) black;
}

#controls {
  border: solid var(--border-width) black;
  height: 5em;
  border-top: none;
  justify-content: space-between;
  align-items: center;
  padding: 0 2em;
  display: flex;
}

#controls > div {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#play, #pause {
  box-sizing: border-box;
  height: 3.5em;
  background: none;
  border-color: #0000 #0000 #0000 #000;
  transition: border-width .1s;
}

#play:hover, #pause:hover {
  transform: scale(1.1);
}

#play {
  border-style: solid;
  border-width: 1.85em 0 1.85em 3em;
}

#pause {
  border-style: double;
  border-width: 0 0 0 3em;
}

#jump {
  width: 7em;
  height: 2.5em;
  background: url("jump.b8088f90.svg") center / contain no-repeat;
}

#jump:hover {
  opacity: .75;
  transform: scale(1.1);
}

#reset {
  width: 3.5em;
  height: 3.5em;
  background: url("reset.8c8db1e1.svg") center / contain no-repeat;
}

#reset:hover {
  opacity: .75;
  transform: scale(1.1);
}

#telemetry {
  position: absolute;
  top: 1em;
  left: 1em;
}

#telemetry > summary {
  cursor: pointer;
  margin-left: .25em;
}

#telemetry > summary:hover {
  transform: scale(1.1);
}

#telemetry > table > tbody > tr > td:nth-child(2) {
  text-align: right;
}

#settings {
  --wrap-width: 17em;
  min-width: var(--wrap-width);
  flex-direction: column;
  flex-basis: var(--wrap-width);
  flex-grow: 1;
  justify-content: space-around;
  display: flex;
}

#settings > ul > li {
  margin: var(--setting-spacing);
}

.checkbox, .checkbox > input {
  cursor: pointer;
}

.tooltip {
  border-bottom: .1em dashed #999;
  text-decoration: none;
}

footer {
  border-top: solid var(--border-width);
  justify-content: space-evenly;
  margin-top: 2.5em;
  padding: 1em;
  display: flex;
}

footer > span {
  align-self: center;
}

footer > a {
  align-items: center;
  display: flex;
}

footer img {
  width: 2em;
  height: 2em;
  margin-left: .5em;
}

